import SearchLink from '@/components/SearchLink'
export default {
  components: {
    SearchLink
  },
  data () {
    return {
      activeIndex: 0,
      // 分类数据
      categories: []
    }
  },
  created () {
    this.getCategories()
  },
  methods: {
		changeIndex(index1){
			this.activeIndex =index1
		},
    toSearchList (catName) {
      wx.navigateTo({ url: '/pages/search_list/main?query=' + catName })
    },
    async getCategories () {
      this.categories = await this.$request({
        url: '/api/public/v1/categories'
      })
    }
  }

}
